<template>
    <ul>
        <li v-for="(item, i) in list" :key="i">
            <i class="u-icon">
                <img v-if="item.status > 0" svg-inline src="../assets/img/works/repo.svg" />
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" fill="#dbab09" v-else="" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 13H3v-1h1v1zm8-6v7c0 .55-.45 1-1 1H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h1V4c0-2.2 1.8-4 4-4s4 1.8 4 4v2h1c.55 0 1 .45 1 1zM3.8 6h4.41V4c0-1.22-.98-2.2-2.2-2.2-1.22 0-2.2.98-2.2 2.2v2H3.8zM11 7H2v7h9V7zM4 8H3v1h1V8zm0 2H3v1h1v-1z"/></svg>
            </i>
            <a
                class="u-title"
                target="_blank"
                :href="postLink(item.id)"
            >[{{ item.type | typeFormat }}] {{ item.title || "" }}</a>
            <div class="u-desc">
                <span class="u-desc-subitem">
                     :
                    <b>{{ item.id }}</b>
                </span>
                <span class="u-status u-desc-subitem">
                    :
                    <b
                        :class="{
                            pass: item.status > 0,
                            pending: item.status == 0,
                            fail: item.status < 0,
                        }"
                    >{{ statusmap[item.status] }}</b>
                </span>
                <time class="u-time u-desc-subitem">: {{ item.createTime | dateFormat }}</time>
            </div>
            <el-button-group class="u-action">
                <el-button size="mini" icon="el-icon-edit" title="" @click="edit(item.id)"></el-button>
                <el-button size="mini" icon="el-icon-delete" title="" @click="del(item.id,i)"></el-button>
            </el-button-group>
        </li>
    </ul>
</template>

<script>
import { types } from "../assets/data/exam.json";
import dateFormat from "../utils/dateFormat";
import { getLink } from "@jx3box/jx3box-common/js/utils";
import { deleteQuestion } from "@/service/exam.js";
const statusmap = {
    "-2": "",
    "-1": "",
    "0": "",
    "1": "",
};
export default {
    name: "question",
    props: ["data"],
    data: function () {
        return {
            statusmap,
            list : this.data || []
        };
    },
    watch : {
        data : function (val){
            this.list = val || []
        }
    },
    methods: {
        edit: function (id) {
            location.href = "./#/question/" + id;
        },
        postLink: function (id) {
            return getLink("question", id);
        },
        del: function (id,i) {
            this.$alert("", "", {
                confirmButtonText: "",
                callback: (action) => {
                    if (action == "confirm") {
                        deleteQuestion(id).then((res) => {
                            this.$message({
                                message: "",
                                type: "success",
                            });
                            this.list.splice(i,1);
                        });
                    }
                },
            });
        },
    },
    filters: {
        dateFormat: function (val) {
            return dateFormat(new Date(val * 1000));
        },
        typeFormat: function (type) {
            return types[type];
        },
    },
    mounted: function () {},
    components: {},
};
</script>

<style scoped lang="less">
@import "../assets/css/work.less";
.m-dashboard-box-list .u-status {
    .pass {
        color: #49c10f;
    }
    .pending {
        color: #fba524;
    }
    .fail {
        color: #fc3c3c;
    }
}
</style>
